:root {
  --padding: 5px;
  --card-size: calc(min(50vh, 100vw) - 2 * var(--padding)); 
}

.markdown, .app {
  box-sizing: border-box;
  padding: var(--padding);
  width: 100vw;
  height: 50vh;
  overflow: auto;
}

.markdown {
  border-bottom: 1px solid white;
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
}

a {
  color: #ccc;
}

a:hover,
a:active {
  color: #fff;
}

@media (min-aspect-ratio: 1/1) {
  :root {
    --card-size: calc(min(100vh, 50vw) - 2 * var(--padding)); 
  }

  #root {
    flex-direction: row;
  }

  .markdown, .app {
    width: 50vw;
    height: 100vh;
  }

  .markdown {
    border-bottom: none;
    border-right: 1px solid #fff;
  }
}