#menu,
#index {
  /* Icon */
  --fillColor: #fff;
  --strokeColor: #000;
  --iconSize: 8vmin;
  --iconOpacity: 0.25;
  --iconHover: 0.625;
  /* Menu */
  --menuColor: #111c;
  --menuBorder: 1px solid #333;
  --menuSize: 35vmin;
  --closedMenu: calc(-1 * var(--menuSize));
  --openIcon: calc(var(--menuSize) - var(--iconSize));
}

#menu {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  z-index: 99;

  color: white;
  text-align: left;
}

#menu svg#openMenu {
  position: absolute;
  width: var(--iconSize);
  height: var(--iconSize);
  fill: var(--fillColor);
  stroke: var(--strokeColor);
  opacity: var(--iconOpacity);
  top: 0;
  left: 0;
  transition: left .3s linear, right .3s linear, opacity .1s;
  transition-property: left, right, opacity;
  transition-delay: 0s, 0s, .3s;
  cursor: pointer;
}

#menu svg#openMenu * {
  pointer-events: none; /* all clicks will be treated by parent */
}

#menu svg#openMenu:hover {
  opacity: var(--iconHover);
  transition: left .3s linear, right .3s linear, opacity .0s;
  transition-property: left, right, opacity;
  transition-delay: 0s, 0s, .0s;
}

#menu #items.open ~ svg#openMenu{
  opacity: 1;
  left: var(--openIcon);
  transition-delay: .1s, .1s, 0s;
}

#menu #items {
  position: absolute;
  left: var(--closedMenu);
  height: 100vh;
  width: var(--menuSize);
  box-sizing: border-box;
  padding: 5PX;
  padding-top: var(--iconSize);
  padding-bottom: 0;
  background-color: var(--menuColor);
  border-right: var(--menuBorder);

  transition: left .40s linear, right .40s linear;

  overflow-y: auto;
}

#menu #items.open {
  left: 0;
  box-shadow: 0 0 3vw 0 rgba(0,0,0,0.99);
  box-shadow: 0 0 3vw 0 rgba(255,255,255,0.250)
}

#menu ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}


#menu #items li {
  box-sizing: border-box;
  margin: 0.25em 0;
  cursor: pointer;
}

#menu #items li:hover {
  background-color: var(--bgColor);
  color: #fff;
}

#menu #items li:active {
  border-style: inset;
}

#menu input[type=radio] {
  position: absolute;
  width: 0;
  opacity: 0;
}

#menu label {
  display: inline-block;
  box-sizing: border-box;
  width: 100%;
  padding: 0.25em 0.5em;
  border: 1px outset #666;
  border-radius: 0.25em;
  color: #888;
}

#menu input[type="radio"]:checked ~ label {
  border-style: inset;
  color: #fff;
  background-color: #000;
}





/* LEFT-HANDED / LEFT-HANDED / LEFT-HANDED LEFT-HANDED / LEFT-HANDED */


main.left-handed #menu,
main.left-handed #menu #items,
main.left-handed #menu #items ~ svg {
  left: auto;
}

main.left-handed #menu,
main.left-handed #menu svg#openMenu {
  right: 0;
}

main.left-handed #menu #items {
  right: var(--closedMenu);
  border-left: var(--menuBorder);
}


main.left-handed #menu #items.open {
  right: 0;
}

main.left-handed #menu #items.open ~ svg#openMenu {
  left: auto;
  right: var(--openIcon);
}